import * as yup from 'yup';
import { TRIP_TYPES } from './consts';

export default yup.object({
  originLocationId: yup
    .number('   ')
    .required('    '),
  tripName: yup
    .string('  ')
    .required(
      '      ',
    ),
  tripType: yup
    .string('  ')
    .oneOf(Object.values(TRIP_TYPES), '   ')
    .required('   '),
});
